<template>
  <section class="rooms-section spad">
    <div class="breadcrumb-section">
      <div class="container">
        <h2></h2>
      </div>
    </div>
    <div class="container" style="padding-bottom: 20%;" v-for="(od ,index) in orders" key="od.OrderNum" v-if="orders.length>0">
      <div class="row" style="padding: 1%">
        <div class="col-lg-4" >
          <div class="contact-text">
            <h3> </h3>
            <hr>
            <img src="../img/hero/hero-1.jpg" alt="" style="height: 200px; width: 100%">
            <hr>
            <p style="font-weight: bold">   .</p>
            <table>
              <tbody>
              <tr>
                <td class="c-o"> </td>
                <td>: {{od.OrderDate.substring(0,10).replaceAll("-",".")}}</td>
              </tr>
              <tr>
                <td class="c-o"> </td>
                <td>: {{od.AddMemberCnt}} </td>
              </tr>
              <tr>
                <td class="c-o">   </td>
                <td v-if="od.PetCnt>0">: {{ od.PetCnt }}, {{ getDateDiff(od.CheckIn.substring(0,10), od.CheckOut.substring(0,10)) }}</td>
                <td v-else>: </td>
              </tr>
              <tr>
                <td class="c-o">  </td>
                <td v-if="od.Breakfast===1">: </td>
                <td v-else>: </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
        <!--        <div class="col-lg-1">
                </div>-->
        <div class="col-lg-7 offset-lg-1">
          <div class="row" style="padding: 1%">
            <div class="col-lg-3" >
              <h3></h3>
              <hr>
            </div>
            <div class="col-lg-8" >
              <h3></h3>
              <hr>
              <div class="row" style="padding: 1%;">
                <div class="col-lg-6" >
                  <p class="pClass">{{ od.Rating }}/{{ getDateDiff(od.CheckIn.substring(0,10), od.CheckOut.substring(0,10)) }} :</p>
                  <p class="pClass">  :</p>
                  <p class="pClass">  :</p>
                  <p class="pClass">  :</p>
                  <p class="pClass">  :</p>
                </div>
                <div class="col-lg-5" >
                  <p class="pClass" style="text-align: right">{{ (od.Price * (getDateDiff(od.CheckIn.substring(0,10), od.CheckOut.substring(0,10)))).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</p>
                  <p class="pClass" style="text-align: right">{{ (od.AddMemberCnt * od.AddPrice).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</p>
                  <p class="pClass" style="text-align: right">{{ (od.PetCnt * 200000).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</p>
                  <p class="pClass" style="text-align: right" v-if="od.Breakfast===1">{{ (getDateDiff(od.CheckIn.substring(0,10), od.CheckOut.substring(0,10)) * 100000 ).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</p>
                  <p class="pClass" style="text-align: right" v-else>0</p>
                  <p class="pClass" style="text-align: right">0</p>
                </div>
              </div>
              <hr>
              <div class="row" style="padding-top: 3%; text-align: center">
                <div class="col-lg-5" >
                  <p style="font-weight: bold; font-size: 22px">   :</p>
                </div>
                <div class="col-lg-6" >
                  <p style="font-weight: bold; text-align: right; font-size: 22px" v-if="od.Breakfast===1">{{ ((od.Price * (getDateDiff(od.CheckIn.substring(0,10), od.CheckOut.substring(0,10)))) +  (od.AddMemberCnt * od.AddPrice) + (od.PetCnt * 200000) + (getDateDiff(od.CheckIn.substring(0,10), od.CheckOut.substring(0,10)) * 100000 )).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</p>
                  <p style="font-weight: bold; text-align: right; font-size: 22px" v-else>{{ ((od.Price * (getDateDiff(od.CheckIn.substring(0,10), od.CheckOut.substring(0,10)))) +  (od.AddMemberCnt * od.AddPrice) + (od.PetCnt * 200000)).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</p>
                </div>
                <button type="button" class="orderBtns" disabled></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container" style="padding-bottom: 20%;" v-else>
      <div class="row" style="padding: 1%">
        <div class="col-lg-4" >
          <div class="contact-text">
            <h3> .</h3>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>

import {defineComponent} from "vue";
import axios from 'axios'

export default defineComponent({
  data() {
    return {
      orders : [],
      userData : {},  //  
    };
  },
  methods : {
    async init() {  // db 
      console.log("order.vue init()")
      if(sessionStorage.getItem("loginYN")){  //  yn ture 
        this.userData = JSON.parse(sessionStorage.getItem("userData")); //  
        console.log(this.userData)
      }

      await axios.post('http://localhost:3000/orders',{ userId : this.userData.UserId })
        .then(res => {
          console.log(res.data)
          this.orders = res.data; //orders  
        })
        .catch(function(error){
          console.log(error);
        })


    },
    getDateDiff(d1, d2){  //   
      const date1 = new Date(d1);
      const date2 = new Date(d2);

      const diffDate = date1.getTime() - date2.getTime();
      return Math.abs(diffDate / (1000 * 60 * 60 * 24)); //  *  *  *  = 
    }
  },
  mounted() { //init 
    this.init();
  }
});

</script>

<style>

.contact-text table tbody tr td.c-o {
  color: #707079;
  width: 200px;
}

.breadcrumb-section {
  padding-top: 60px;
  padding-bottom: 40px;
}

.pClass{
  padding-top: 5%;
  font-size: 20px;
}

.orderBtns{
  display: block;
  font-size: 14px;
  text-transform: uppercase;
  border: 1px solid #dfa974;
  border-radius: 2px;
  color: #dfa974;
  font-weight: 500;
  background: transparent;
  width: 85%;
  height: 46px;
  margin-top: 10px;
  margin-left: 4%;
}

</style>