 <script>
 import axios from 'axios';
 export default {
   name: "join",
 data () {
     return {
       id: "",
       password: null,
       idValid: true,
       name:"",
       ssn: "",
       email:"",
       phone:"",
       passwordCheck: '',
       passwordValidFlag: true,
       passwordCheckFlag: true,
       zonecode: "",
       roadAddress: "",
       detailAddress: "",
       JoinDate: new Date(),
     }
  
   },
   computed: {
    
     valid() {
       if(/^[A-Za-z0-9]+$/.test(this.id)){
        this.idValid = true
       }else{
        this.idValid = false
       }
       
     }
   },
   methods: {
     passwordValid () {
       if (/^(?=.*[a-z])(?=.*[0-9]).{8,16}$/.test(this.password)) {
         this.passwordValidFlag = true
       } else {
         this.passwordValidFlag = false
       }
     },
     passwordCheckValid () {
       if (this.password === this.passwordCheck) {
         this.passwordCheckFlag = true
       } else {
         this.passwordCheckFlag = false
       }
     },
     async userIdMatch(id){           
         try {
            //  console.log(this.id)
          let data = await axios.post("http://localhost:3000/finds",{
            UserId: this.id
            });
        
            console.log(data.data[0].UserId)
            console.log(this.id)
          if(this.id === data.data[0].UserId){
            alert("")
          }
        }
        catch(err){
          alert("")
        }
      },
     openPostcode() {
       new window.daum.Postcode({
         oncomplete: (data) => {
           this.zonecode = data.zonecode;
           this.roadAddress = data.roadAddress;
         },
       }).open();
     }, 
    async signUpButton() {
       try {
         await axios.post('http://localhost:3000/create', {
           UserId: this.id,
           UserPw: this.password,
           UserName: this.name,
           UserPhNum1 : this.phone.replaceAll("-","").substring(0,3),
           UserPhNum2 : this.phone.replaceAll("-","").substring(3,7),
           UserPhNum3 : this.phone.replaceAll("-","").substring(7,11),
           UserEmail: this.email,
           UserSsn1 : this.ssn.replaceAll("-","").substring(0,6),
           UserSsn2 : this.ssn.replaceAll("-","").substring(6,13),    
           Admin : 0,
           JoinDate : this.getFormatDate(this.JoinDate),
         });
       
         this.$router.push("/login");
 
         alert(" ");
       } catch (err) {
        console.log(err)
         console.log(" !!!!");

       }
     },
     getSsnMask(val)
     {
      let res = val;
      if(res.length === 6){
        this.ssn = res + "-";
      }
    },
     getPhoneMask(val) {
      let res = val;
      if(res.length === 3 || res.length === 8){
        this.phone = res + "-";
      } 
    }  ,
    getFormatDate(date) {
      const YYYY = String(date.getFullYear())
      const MM = String((date.getMonth() + 1) >= 10 ? (date.getMonth() + 1): "0" + (date.getMonth() + 1))
      const dd = String(date.getDate() >= 10 ? date.getDate() : "0" + date.getDate())
      return YYYY + "." + MM + "." + dd
    }
  }
}

 
 
 </script>
 
