<template>
  <section class="rooms-section spad">
    <div class="breadcrumb-section">
      <div class="container">
        <h2> </h2>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-lg-4 col-md-6" v-for="(rm, index) in room" key="rm.RoomNum">
          <div class="room-item">
            <img :src="rm.RoomImg" alt="">
            <div class="ri-text kmh">
              <table>
                <tbody>
                <tr>
                  <td class="r-o"> :</td>
                  <td>{{rm.Rating}}</td>
                </tr>
                <tr>
                  <td class="r-o">  :</td>
                  <td>{{rm.Size+""}}</td>
                </tr>
                <tr>
                  <td class="r-o">  :</td>
                  <td>{{rm.BasicPersonnel+""+" "+"~"+" "+rm.MaxPersonnel+""}}</td>
                </tr>
                <tr>
                  <td class="r-o"> :</td>
                  <td>{{rm.Bed}}</td>
                </tr>
                <tr>
                  <td class="r-o"> :</td>
                  <td v-if="rm.Bath==1 && rm.Terrace==1 && rm.Spa==1 && rm.Pool==0 && rm.Rooftop==0">, , </td>
                  <td v-if="rm.Bath==1 && rm.Terrace==1 && rm.Spa==1 && rm.Pool==1 && rm.Rooftop==0">,, </td>
                  <td v-if="rm.Bath==1 && rm.Terrace==1 && rm.Spa==1 && rm.Pool==1 && rm.Rooftop==1">, ,</td>
                </tr>
                </tbody>
              </table>
              <!--              <router-link to="/roomDetail"><a href="#" class="primary-btn"> </a></router-link>-->
                <a href="#" class="primary-btn" @click="nextPage(index)"> </a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
<!--    <div class="col-lg-12">-->
<!--      <div class="room-pagination">-->
<!--        <a href="#">1</a>-->
<!--        <a href="#">2</a>-->
<!--        <a href="#">Next <i class="fa fa-long-arrow-right"></i></a>-->
<!--      </div>-->
<!--    </div>-->
  </section>


</template>
<style>
.room-item{
  border-radius: 1%;
  overflow: hidden;
  word-break: keep-all;
}

.room-item .ri-text {
  border: 2px double #ebebeb;
  border-top: none;
  padding: 24px 24px 30px 28px;
}

.breadcrumb-section {
  padding-top: 60px;

}


</style>

<script>
import {defineComponent} from "vue";
import axios from "axios";
import router from "@/router";

export default defineComponent({
  data() {
    return {
      room: [],
      userData : {},
    };
  },
  methods :{
    init() { //db 
      axios.get('http://localhost:3000/rooms')
          .then(res => {
            console.log(res.data)
            this.room = res.data; //room  
          })
          .catch(function (error) {
            console.log(error);
          })
      if(sessionStorage.getItem("loginYN")) {  //  yn ture 
        this.userData = JSON.parse(sessionStorage.getItem("userData")); //  
        console.log(this.userData)
      }
    },
    nextPage(index){  //for  
      console.log(index)
      console.log(this.room[index])
      router.push({
        name: "roomDetail",
        state: {  //
          dataObj : JSON.stringify(this.room[index]),  //dataObj : ()
        }
      })
    }
    //     URL  
    },
    mounted() { //init 
      this.init();
    }
  });
</script>